var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticStyle:{"font-size":"12px","font-weight":"300","padding":"5px 15px"}},[_c('h2',{staticStyle:{"text-align":"center","font-weight":"400"}},[_vm._v("优速金融隐私权保护声明")]),_vm._v(" "),_c('p',{staticStyle:{"text-align":"center"}},[_vm._v("引言")]),_vm._v("\n优速金融（包括优速金融及其关联公司，以下亦称“我们”）非常重视用户个人信息及隐私权的保护， 因此我们制订了涵盖如何收集、存储、使用、共享和保护用户信息的隐私政策。我们希望通过本优速金融隐私权保护声明 （“本隐私政策”）向您清晰地介绍我们对您个人信息的处理方式，本隐私政策与您使用优速金融的产品和/或服务息息相关， 请您在使用优速金融的所有产品和/或服务前，完整阅读并透彻理解本隐私政策，并相应作出您认为适当的选择。 如果您不同意本隐私政策的任何内容，您应立即停止使用优速金融产品和/或服务。当您使用优速金融任一产品和/或服务时， 则表示您同意且完全理解本隐私政策的全部内容。"),_c('br'),_vm._v("\n一、本隐私政策的适用范围"),_c('br'),_vm._v("\n1.1 优速金融平台所有的产品和服务（包括优速金融所有的产品或服务及未设独立隐私政策的优速金融关联公司的产品或服务） 均适用本隐私政策，但某些特定产品和/或服务还将适用特定的隐私政策，我们将在向您提供这些特定产品和/或服务前说明。"),_c('br'),_vm._v("\n1.2 上述特定产品和/或服务的隐私政策构成本隐私政策的一部分，如与本隐私政策有不一致之处，以特定产品和/或服务的隐私政策为准。"),_c('br'),_vm._v("\n1.3 除非本隐私政策另有明确所指，本隐私政策不适用于通过我们的产品和/或服务而接入的由第三方独立为您提供的产品和/或服务的情形， 例如在搜索结果中向您显示的产品或网站、可能包含优速金融服务的网站或者在我们的服务中链接到的其他网站。请注意， 如果您使用第三方的产品和／或服务时向其提供您的个人信息，您的信息应当适用该第三方的隐私声明或类似政策， 我们对任何第三方不当使用或披露由您提供的信息不承担任何法律责任。"),_c('br'),_vm._v("\n二、我们可能收集的信息"),_c('br'),_vm._v("\n2.1 您向优速金融提供的信息"),_c('br'),_vm._v("\n（1）您注册优速金融帐号时须至少向我们提供帐号名称、手机号码及或电子邮箱，并创建密码。"),_c('br'),_vm._v("\n（2）您使用优速金融特定产品或服务时（如申请信贷、购买产品或服务、导航、建立个人主页、发表文章等）， 为满足向您提供产品和服务之目的，除注册时提供的信息外，您还需要进一步向我们提供您的真实姓名、性别、 出生日期、身份证号码、常用地址、银行卡、头像、简介、运营商、社保、公积金和电商等信息，如果您不使用特定产品和服务，则无需提供相关信息。"),_c('br'),_vm._v("\n（3）某些个人信息（如您的种族、宗教、个人健康和医疗信息等）因其特殊性可能被认为是敏感个人信息。 您在使用我们的产品和/或服务时由您主动提供、上传或发布的内容和信息（例如有关您社交活动的照片等信息）， 可能会泄露您的敏感个人信息，请您谨慎对待。"),_c('br'),_vm._v("\n2.2 在您使用产品或服务过程中收集的信息。"),_c('br'),_vm._v("\n（1）设备信息。 为了提供更好的产品和/或服务并改善用户体验，我们会收集设备属性信息（例如您的硬件型号、 操作系统版本、设备配置、唯一设备标识符、国际移动设备身份码IMEI、网络设备硬件地址MAC、广告标识符IDFA等）， 设备位置信息（如通过GPS、蓝牙或WIFI信号获得的位置信息）、设备连接信息（浏览器的类型、电信运营商、使用的语言） 以及设备状态信息（例如设备传感器数据，设备应用安装列表）。对于从您的各种设备上收集到的信息，我们可能会将它们进行关联， 以便我们能在这些设备上为您提供一致的服务。我们可能会将您的设备信息或电话号码与您的优速金融帐户相关联。"),_c('br'),_vm._v("\n（2）日志信息。 当您使用优速金融的产品和 / 或服务时，我们的服务器会自动记录一些信息，例如您对我们的产品和 / 或服务的使用情况、 IP地址、所访问服务的URL、浏览器的类型和使用的语言、下载、安装或使用移动应用和软件的信息、 与通讯软件通讯的信息以及访问服务的日期、时间、时长等。"),_c('br'),_vm._v("\n（3）位置信息。 当您使用具有定位功能的优速金融产品和/或服务时，当您开启设备定位功能并使用我们基于位置提供的相关服务时， 我们可能会收集和处理有关您实际所在位置的信息，以使得您不需要手动输入自身地理坐标就可获得相关服务。 我们会使用各种技术进行定位，这些技术包括 IP 地址、GPS 以及能够提供相关信息的其他传感器 （比如可能会为优速金融提供附近设备、Wi-Fi 接入点和基站的信息）。您可以通过关闭定位功能， 停止对您的地理位置信息的收集，但您可能将无法获得相关服务或功能，或者无法达到相关服务拟达到的效果。"),_c('br'),_vm._v("\n（4）唯一应用程序编号。 某些产品和/或服务包含唯一应用程序编号，当您安装、激活、更新、卸载相关产品和/或服务或当这些产品和/或服务定期与优速金融通信 （例如软件的更新）时，系统会将此编号以及与安装相关的信息（例如操作系统类型和应用程序版本号）发送给优速金融。"),_c('br'),_vm._v("\n（5）本地存储。 我们可能会使用浏览器网络存储等机制（包括HTML5）和应用数据缓存，收集您设备上的信息并进行本地存储。"),_c('br'),_vm._v("\n（6）Cookie和匿名标示符等工具。 cookie主要的功能是便于您使用网站产品和/或服务，以及帮助网站统计独立访客数量等。运用cookie技术， 优速金融能够为您提供更加周到的个性化服务，并允许您设定您特定的服务选项。"),_c('br'),_vm._v("\n三、来自第三方的信息"),_c('br'),_vm._v("\n3.1 为共同向您提供产品/服务或改进产品服务的质量和个性化程度或出于对产品和服务安全性的考量等合理需要， 我们将按照法律法规的规定或基于您的授权从关联公司、联盟成员、合作伙伴及其他受信任的第三方供应商、 服务商及代理商处接收您的个人信息及其他信息。"),_c('br'),_vm._v("\n四、信息安全"),_c('br'),_vm._v("\n4.1 优速金融非常重视信息安全，泛舟成立了专责团队负责研发和应用多种安全技术和程序等， 通过这些严格措施努力保护您的信息不被未经授权的访问、使用或泄漏。但请您理解， 在互联网行业由于技术的限制和飞速发展以及可能存在的各种恶意攻击手段，即便优速金融竭尽所能加强安全措施， 也不可能始终保证信息的百分之百安全。请您了解，您使用我们的产品和/或服务时所用的系统和通讯网络， 有可能因超出我们控制能力之外的其他因素而出现安全问题。"),_c('br'),_vm._v("\n五、未成年人使用我们的产品和/或服务"),_c('br'),_vm._v("\n5.1 优速金融非常重视对未成年人信息的保护。若您是18周岁以下的未成年人， 建议您请您的监护人仔细阅读本隐私政策，并在征得您的监护人同意的前提下使用我们的产品和/或服务或向我们提供信息。"),_c('br'),_vm._v("\n六、隐私政策的修订/或服务"),_c('br'),_vm._v("\n6.1 我们可能对本隐私政策进行不时修订，该等修订构成本隐私政策的一部分。我们会在本页面上或通过其他我们认为合适的方式 （包括向您发送电子邮件或以其他方式通知您）发布对本隐私政策所做的任何变更。若您继续使用我们的产品和/或服务， 即表示您同意受修订后的本隐私政策的约束。"),_c('br'),_vm._v("\n您可以通过如下联络方式同优速金融联系："),_c('br'),_vm._v("\n优速金融　产品事务组"),_c('br')])}]
